const toggleTabs = () => {
  const usaCollectionTabHeaderItem = document.querySelectorAll('.usa-collection__tab-header_item');
  const usaCollectionTabContent = document.querySelectorAll('.usa-collection__tab-content');
  const main = document.querySelector('.main');

  const benefitsTabHeaderItem = document.querySelectorAll('.benefits-tab__header-item');
  const benefitsTabContentItem = document.querySelectorAll('.benefits-tab__content-item');

  const trustHeaderSlide = document.querySelectorAll('.trust-header__slide');
  const trustContentItem = document.querySelectorAll('.trust-content__item');

  const togglecollectionTab = (index) => {
    for (let i = 0; i <= usaCollectionTabContent.length; i++) {
      if (index === i) {
        usaCollectionTabContent[i].classList.add('active');
        usaCollectionTabHeaderItem[i].classList.add('active');
      } else {
        usaCollectionTabContent[i].classList.remove('active');
        usaCollectionTabHeaderItem[i].classList.remove('active');
      }
    }
  };

  const toggleBenefitsTab = (index) => {
    for (let i = 0; i <= benefitsTabContentItem.length; i++) {
      if (index === i) {
        benefitsTabContentItem[i].classList.add('active');
        benefitsTabHeaderItem[i].classList.add('active');
      } else {
        benefitsTabHeaderItem[i].classList.remove('active');
        benefitsTabContentItem[i].classList.remove('active');
      }
    }
  };

  const toggleTrustTab = (index) => {
    for (let i = 0; i <= trustContentItem.length; i++) {
      if (index === i) {
        trustHeaderSlide[i].classList.add('active');
        trustContentItem[i].classList.add('active');
      } else {
        trustHeaderSlide[i].classList.remove('active');
        trustContentItem[i].classList.remove('active');
      }
    }
  };

  main.addEventListener('click', (e) => {
    e.preventDefault();
    /* eslint-disable-next-line */
    const target = e.target;

    if (target.closest('.usa-collection__tab-header_item')) {
      usaCollectionTabHeaderItem.forEach((item, i) => {
        if (item === target.closest('.usa-collection__tab-header_item')) {
          togglecollectionTab(i);
        }
      });
    } else if (target.closest('.benefits-tab__header-item')) {
      benefitsTabHeaderItem.forEach((item, i) => {
        if (item === target.closest('.benefits-tab__header-item')) {
          toggleBenefitsTab(i);
        }
      });
    } else if (target.closest('.trust-header__slide')) {
      trustHeaderSlide.forEach((item, i) => {
        if (item === target.closest('.trust-header__slide')) {
          toggleTrustTab(i);
          console.log('hi');
        }
      });
    }
  });
};

toggleTabs();

const toggleBidPopup = () => {
  const main = document.querySelector('.main');
  const bidPopup = document.querySelector('.bid-popup');

  main.addEventListener('click', (e) => {
    if (e.target.closest('.benefits-tab__btn') || e.target.closest('.card-btn')) {
      bidPopup.style.display = 'block';
    }
  });

  bidPopup.addEventListener('click', (e) => {
    if (e.target.classList.contains('bid-popup__wrapper') || e.target.closest('.form-cross')) {
      bidPopup.style.display = 'none';
    }
  });
};

toggleBidPopup();

const ports = () => {
  const tooltip = document.querySelectorAll('.tooltip');
  const map = document.querySelector('.usa-map');
  const tooltipWrapper = document.querySelectorAll('.tooltip__wrapper');
  const posrtsMapInfo = document.querySelectorAll('.posrts-map__info');

  const toggleTab = (index) => {
    for (let i = 0; i < posrtsMapInfo.length; i++) {
      if (index === i) {
        posrtsMapInfo[i].classList.add('active');
        tooltipWrapper[i].classList.add('active');
        tooltip[i].classList.add('active');
      } else {
        posrtsMapInfo[i].classList.remove('active');
        tooltipWrapper[i].classList.remove('active');
        tooltip[i].classList.remove('active');
      }
    }
  };

  map.addEventListener('mouseover', (e) => {
    const target = e.target.closest('.tooltip__wrapper');
    console.log(target);
    tooltipWrapper.forEach((item, i) => {
      if (item === target) {
        toggleTab(i);
      }
    });
  });
};

ports();

const useAccordion = () => {
  const accordionItem = document.querySelectorAll('.accordion__item');
  const contactsAccordionItem = document.querySelectorAll('.contacts-accordion__item');

  const open = (button, dropdown) => {
    /* eslint-disable-next-line */
    closeAllDrops();
    /* eslint-disable-next-line */
    dropdown.style.height = "".concat(dropdown.scrollHeight + 20, "px");
    button.classList.add('active');
    dropdown.classList.add('active');
  };

  const close = (button, dropdown) => {
    button.classList.remove('active');
    /* eslint-disable-next-line */
    dropdown.classList.remove('active');
    /* eslint-disable-next-line */
    dropdown.style.height = '';
  };

  const closeAllDrops = function closeAllDrops(button, dropDown) {
    /* eslint-disable-next-line */
    accordionItem.forEach(function (item) {
      if (item.children[0] !== button && item.children[1] !== dropDown) {
        close(item.children[0], item.children[1]);
      }
    });
    /* eslint-disable-next-line */
    contactsAccordionItem.forEach(function (item) {
      if (item.children[0] !== button && item.children[1] !== dropDown) {
        close(item.children[0], item.children[1]);
      }
    });
  };

  document.body.addEventListener('click', (e) => {
    /* eslint-disable-next-line */
    const target = e.target;
    console.log(target);
    if (target.closest('.accordion-title')) {
      const parent = target.closest('.accordion__item');
      const button = target.closest('.accardion-btn__wrapper');
      const dropdown = parent.querySelector('.accordion-description');
      /* eslint-disable-next-line */
      dropdown.classList.contains('active') ? close(button, dropdown) : open(button, dropdown);
    } else if (target.classList.contains('contacts-accordion__title')) {
      console.log('hi');
      const parent = target.closest('.contacts-accordion__item');
      const button = target.closest('.contacts-accordion__title');
      const dropdown = parent.querySelector('.contacts-accordion__description');
      /* eslint-disable-next-line */
      dropdown.classList.contains('active') ? close(button, dropdown) : open(button, dropdown);
    }
  });
};

useAccordion();

const secrets = () => {
  const secretsTab = document.querySelector('.secrets-header__tab');
  const secretsHeaderTabItem = document.querySelectorAll('.secrets-header__tab-item');
  const secretsContentItem = document.querySelectorAll('.secrets-content__item');

  const toggleTab = (index) => {
    for (let i = 0; i < secretsContentItem.length; i++) {
      if (index === i) {
        secretsContentItem[i].classList.add('active');
        secretsHeaderTabItem[i].classList.add('active');
      } else {
        secretsContentItem[i].classList.remove('active');
        secretsHeaderTabItem[i].classList.remove('active');
      }
    }
  };

  secretsTab.addEventListener('click', (e) => {
    const target = e.target.closest('.secrets-header__tab-item');
    secretsHeaderTabItem.forEach((item, i) => {
      if (item === target) {
        toggleTab(i);
      }
    });
  });
};

secrets();

const scrollTo = () => {
  document.body.addEventListener('click', (e) => {
    if (e.target.closest('a[href^="#"]')) {
      e.preventDefault();
      const target = e.target.closest('a[href^="#"]').getAttribute('href');
      document.querySelector(target).scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
    }
  });
};

scrollTo();

